<template>
    <div class="hc-address-add">
        <mt-field label="收货人" placeholder="请填写收货人姓名" v-model="receiverName"></mt-field>
        <mt-field label="联系电话" placeholder="请填写联系电话" type="tel" v-model="receiverPhoneNo"></mt-field>
        <!-- <mt-cell title="所在地区" is-link> </mt-cell> -->
        <mt-field label="所在地区" placeholder="所在城市及区县" v-model="address" disabled @click.native="openAddress()" @click="openAddress">
          <icon value='lujing11'></icon>
        </mt-field>
        <mt-field label="详细地址" placeholder="街道、小区、楼牌号等" v-model="detailAddress"></mt-field>
        <mt-cell title="设置为默认">
          <mt-switch v-model="defaultAddress"></mt-switch>
        </mt-cell>
        <div class="hc-address-btn">
            <mt-button @click="addShippingHandle">确定</mt-button>
        </div>
        <hc-address ref='address' @confirm="changeAddress"></hc-address>
    </div>
</template>
<script>
import { addShipping } from '../../api/axios.js';
export default {
  data(){
     return{
        receiverName:null,
        receiverPhoneNo:null,
        address:null,
        detailAddress:null,
        defaultAddress:true,
        addressData:null
     }
  },
  methods:{
     addShippingHandle(){
       const data = {
         receiverName:this.receiverName,
         receiverPhoneNo:this.receiverPhoneNo,
         provinceId: this.addressData[0].nodeCode,
         provinceName:this.addressData[0].nodeName,
         cityId:this.addressData[1].nodeCode,
         cityName:this.addressData[1].nodeName,
         districtId:this.addressData[2].nodeCode,
         districtName:this.addressData[2].nodeName,
         detailAddress:this.detailAddress,
         defaultAddress:this.defaultAddress ? 1 : 0
       }
        let formData = new FormData();
        Object.keys(data).forEach(function(key){
            formData.append(key, data[key]);
        });
        addShipping(formData).then((data) => {
          if(data.code == 200){
            this.$router.push('/mdcOrder/ConfirmOrder');
          }
        })
     },
     changeAddress(value){
        console.info(value)
        this.addressData = value;
        this.address = `${value[0].nodeName} ${value[1].nodeName} ${value[2].nodeName}`
     },
     openAddress(){
       this.$refs.address.open();
     }
  }
}
</script>
<style lang="scss" scoped>
   .hc-address-add{
      .hc-address-btn{
          margin-top: px2rem(98);
          text-align: center;
          .mint-button--default{
             width: px2rem(340);
             height: pxwrem(45);
             background-color: #f3ab03;
             color: #fff;
             box-shadow:0;
          }
      }
   }
</style>




